import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { CursosviewContenidoPage} from '../cursosview-contenido/cursosview-contenido';
import { CursostabrrViewPage} from '../cursostabrr-view/cursostabrr-view';
import {CursostabnnViewPage} from '../cursostabnn-view/cursostabnn-view';
import {CursostabchViewPage} from '../cursostabch-view/cursostabch-view';
import { Auth  } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';

import { Storage } from '@ionic/storage';
import { Socket } from 'ng-socket-io';

@IonicPage()
@Component({
  selector: 'page-cursostab-view',
  templateUrl: 'cursostab-view.html',
})
export class CursostabViewPage {
  public tab1Root: any;
  public tab2Root: any;
  public tab3Root: any;
  public tab4Root: any;
  idt:any;
  titulox: any;
  idempresa:any;
  tiposus: any=[];
  idsuscriptor: any;
  fotott: any;
  nombrett: any;
para:any;
  constructor(  public translateService: TranslateService, public storage: Storage,public regService: Auth,private socket: Socket,public navCtrl: NavController, public navParams: NavParams) {
    this.titulox=this.navParams.data.nombrep;
    this.idt=this.navParams.data.idt
    this.tab1Root = CursosviewContenidoPage;
    this.tab2Root = CursostabrrViewPage;
    this.tab3Root = CursostabnnViewPage;
    this.tab4Root = CursostabchViewPage;
  }

  ionViewDidLoad() {
   
    
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
     
  })
    

  
    this.storage.get('tiposus').then((value) => {
      this.tiposus = value.split(',');
    this.idsuscriptor=this.tiposus[3]
  this.nombrett=this.tiposus[4]   
  this.fotott=this.tiposus[9]+","+this.tiposus[10]
  

    });
  
  
  
    
    }

  iniciarcurso(){    
   
   


    this.regService.getReg(this.idt+"@gmail.com"+ '/emailsolo/' + this.idempresa,'personals').then((data) => {
      this.socket.connect();
    this.socket.emit('set-nickname', data[0]._id);
    this.navCtrl.push('ChatPage', { record :{  toUserId:this.idsuscriptor,
      toUserName:this.nombrett,avatar:this.fotott} ,grupal:'si',
      record2:{  toUserId:data[0]._id,
    toUserName: 'Aporta al modulo',avatar:data[0].foto}});
    });
    
  }
 
}



